@import "variables";

@import url("https://fonts.googleapis.com/css?family=Montserrat:400,500,600,700&display=swap");

html,
body {
  font-family: "Merriweather";
  font-weight: 400;
  font-size: 1em;
  background-color: $midnight;
  color: $dune;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "Montserrat";
  margin-bottom: 1rem;
  font-weight: 700;
  color: $icehot;
}

h1 {
  font-size: 1.75rem;
}
h2 {
  font-size: 1.6rem;
}

h3 {
  font-size: 1.5rem;
  font-weight: 600;
}

h4 {
  font-size: 1.4rem;
  font-weight: 600;
}
h5 {
  font-size: 1.3rem;
  font-weight: 500;
}
h6 {
  font-size: 1.25rem;
  font-weight: 500;
}

p {
  font-family: "Montserrat";
  font-size: 1rem;
  line-height: 1.5rem;
  font-weight: 400;
  margin-bottom: 1rem;
}

code {
  margin: 1rem 0;
  background-color: grey;
}

.container {
  width: 90%;
  margin: 0 auto;
  @media (min-width: $breakpoint-lg) {
    width: 80%;
  }
  @media (min-width: $breakpoint-ex-lg) {
    width: 60%;
  }
}

a:link,
a:active,
a:hover,
a:visited {
  text-decoration: none;
  color: $icehot;
}
